<template>
  <div>
    <el-button type="primary" @click="$router.push('menu/add')">添加<i class="el-icon-circle-plus-outline"></i></el-button>
    <div class="table-bg">
      <el-table :data="list" style="width: 100%;margin-bottom: 20px;"
          row-key="id"
          default-expand-all
          :tree-props="{children: 'children'}">
        <el-table-column label="菜单ID" prop="id"></el-table-column>
        <el-table-column label="菜单名称" prop="title"></el-table-column>
        <el-table-column label="菜单类型" prop="type">
           <template slot-scope="scope">
              <el-tag v-if="scope.row.type==1">目录</el-tag>
              <el-tag  type="success" v-if="scope.row.type==2">菜单</el-tag>
            </template>
        </el-table-column>
        <el-table-column label="菜单图标" prop="icon">
           <template slot-scope="scope">
              <i :class="scope.row.icon"></i>
            </template>
        </el-table-column>
        <el-table-column label="菜单地址" prop="url"></el-table-column>
        <el-table-column label="状态" prop="status">
          <template slot-scope="scope">
              <el-tag v-if="scope.row.status==1" type="success">正常</el-tag>
              <el-tag  v-else type="info">禁用</el-tag>
            </template>
        </el-table-column>
        <el-table-column label="操作" prop="status">
            <template slot-scope="scope">
              <el-button @click="$router.push('/menu/edit/'+scope.row.id)"  icon="el-icon-edit"  type="success" circle size="small"></el-button>
              <el-button @click="del(scope.row.id)" type="danger" icon="el-icon-delete" circle size="small"></el-button>
            </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import { mapGetters,mapActions } from "vuex"
export default {
  data() {
    return {
    };
  },
  computed: {
    ...mapGetters({
      list:"menu/menulist"
    })
  },
  mounted() {
    if(!this.list.length){
      this.getlist();
    }
  },
  methods: {
     ...mapActions({
       getlist:"menu/getlist",
       del:"menu/del"
     })
  },
  components: {}
};
</script>
<style scoped>
</style>


